export default {
	data() {
		return {
			list: [],
		}
	},
	onLoad: function(e) {
		this.$queue.showLoading("加载中...");
		this.getMoney();
	},

	methods: {
		getMoney() {
			let that = this;
			let token = this.$queue.getData("token");
			let relationId = this.$queue.getData("relation_id");
			if (token) {
				//可以提现金额查询预估收入查询
				this.$Request.getT("/cash/list/" + relationId).then(res => {
					if (res.status === 0 && res.data) {
						that.list = res.data;
					}
					uni.hideLoading();
				});
			}

		},
	},

}
